<script>
import RedditList from '../components/RedditList.vue'

export default {
  components: {
    RedditList
  }

}
</script>

<template>
<div>

  <form id="gform" method="POST" action="https://script.google.com/macros/s/AKfycbwFCzjFlMtKGId0AsGBs8dNev5Ih9o5Qie76CNM7GmS-Tk_dVOj/exec">
  <p>Dear Andrew,</p>
  <p>My
    <label for="name">name</label> is
    <input type="text" name="name" id="name" minlength="3" placeholder="(your name here)" required> and</p>

  <p>my
    <label for="email">email address</label> is
    <input type="email" name="email" id="email" placeholder="(your email address)" required>
  </p>

  <p> I want more information about...</p>

  <p>
    <textarea name="message" id="message" placeholder="(your msg here)" class="expanding" required></textarea>
  </p>
  <p>
    <button type="submit" class="button-success" v-on:click="greet">
      <svg version="1.1" class="send-icn" xmlns="http://www.w3.org/2000/svg" xmlns:xlink="http://www.w3.org/1999/xlink" x="0px" y="0px" width="100px" height="36px" viewBox="0 0 100 36" enable-background="new 0 0 100 36" xml:space="preserve">
        <path d="M100,0L100,0 M23.8,7.1L100,0L40.9,36l-4.7-7.5L22,34.8l-4-11L0,30.5L16.4,8.7l5.4,15L23,7L23.8,7.1z M16.8,20.4l-1.5-4.3
  l-5.1,6.7L16.8,20.4z M34.4,25.4l-8.1-13.1L25,29.6L34.4,25.4z M35.2,13.2l8.1,13.1L70,9.9L35.2,13.2z" />
      </svg>
      <small>send</small>
    </button>
  </p>
</form>

  <!-- Customise the Thankyou Message People See when they submit the form: -->
  <div style="display:none;" id="thankyou_message">
    <h2><em>Thanks</em> for contacting me!
      I'll get back to you soon!</h2>
  </div>

</div>

</template>

<style lang="scss">

#gform {
  max-width: 90%;
  margin: 0 auto;
  background: #ffd400;
  padding: 10px 10px;
  font-family: 'Roboto Condensed', sans-serif;
  font-weight: 300;

  p {
      margin: 10px 0px;
  }

  p > label {
    font-family: 'Roboto Condensed', sans-serif;
    font-weight: 700;
    cursor: pointer;
  }

  textarea,
  input {
    border: none;
    outline: none;
    border-radius: 0;
    text-align: center;
    background: none;
    font-weight: 700;
    font-family: 'Lato', georgia;
    font-size: 14px;
    color: rgb(82, 121, 150);
    max-width: 90%;
    padding: 5px 15px;
    border: 2px dashed rgba(255, 255, 255, 0);
    box-sizing: border-box;
    cursor: text;
  }

  textarea {
    text-align: left;
    /* overflow:hidden; */
    resize: none;
    width: 90%;
    border-color: rgba(255, 255, 255, 0);
    min-height: 80px;
  }

  textarea:focus {
    background-color: rgba(255, 255, 255, 0.2);
    border: 2px dashed rgb(183, 152, 0);
  }

  textarea:focus:required:valid {
    border: 2px solid rgba(255, 255, 255, 0);
    border-bottom: 2px solid rgba(255, 255, 255, 0.2);
  }

  textarea:required:valid {
    border-bottom: 2px solid rgba(255, 255, 255, 0.2);
  }

  input {
    border-bottom: 2px dashed rgb(183, 152, 0);
    font-size: 16px;
  }

  input:required,
  textarea:required {
    border-bottom: 2px dashed rgb(183, 152, 0);
  }

  input:focus {
    border-bottom: 2px dashed rgba(255, 255, 255, 1);
    background-color: rgba(255, 255, 255, 0.2);
  }

  input:required:valid {
    border-bottom: 2px solid rgba(255, 255, 255, 0.2);
  }

  input:required:invalid {
    color: rgba(255, 255, 255, 0.5);
    text-decoration: line-through;
  }

  ::-webkit-input-placeholder {
    text-align: center;
    color: rgb(82, 121, 150);
    font-style: italic;
    font-weight: 400;
  }

  :-moz-placeholder {
    /* Firefox 18- */
    text-align: center;
    color: rgb(82, 121, 150);
    font-style: italic;
    font-weight: 400;
  }

  ::-moz-placeholder {
    /* Firefox 19+ */
    text-align: center;
    color: rgb(82, 121, 150);
    font-style: italic;
    font-weight: 400;
  }

  :-ms-input-placeholder {
    text-align: center;
    color: rgb(82, 121, 150);
    font-style: italic;
    font-weight: 400;
  }

  .expanding {
    vertical-align: top;
  }

  .send-icn {
      fill: rgb(82, 121, 150);
      transition: .2s;
      margin-bottom: 5px;
  }

  .send-icn:hover {
  transform: scale(1.2);
  transition: .2s;
  cursor: pointer;
  }

  button {
    background: none;
    border: none;
    outline: none;
    margin-top: 20px;
    font-family: 'Roboto Condensed', sans-serif;
    font-weight: 700;
    cursor: pointer;
    color: rgb(82, 121, 150);
  }

  button:hover {
  transition: .2s;
  }

  button:hover small {
    opacity: 1;
    transition: .2s;
  }

  small {
    display: block;
    opacity: 0;
  }

  .website {
    opacity: 1;
    font-size: 16px;
    color: white;
    position: relative;
    text-align: center;
    display: block;
    margin-top: 7%;
    
  }

  .website a {
    color: white;
  }

}

@media (min-width:767px) {
  #gform {
   min-width: 700px;
  }
}

</style>


